require('../css/base.css');
require('../css/public.css');
require('../css/study.css');
require('./base.js');
require('./jquery.waypoints.min.js');
require('./scrollmoniter.js');
var header=require('../tmpl/header.html');
var footer=require('../tmpl/footer.html');
$('#header').html(header);
$('#footer').html(footer);
require('./nav.js');//先载入header模板，base才能操作其中dom

/* ------------------------------------------------------------
 * 章程切换
 * ------------------------------------------------------------ */
var consitList=$('.constituList').children().filter('li');
var consitConentList=$('.constituContent').children();
var constituContentImg=$('.constituContentImg').children();
consitList.each(function(){
    var $this=$(this);
    var index=$this.attr('index');
    $this.click(function(){
        consitList.filter('.active').removeClass('active');
        $this.addClass('active');
        consitConentList.filter('.block').removeClass('block').addClass('none');
        consitConentList.eq(index).removeClass('none').addClass('block');
        constituContentImg.filter('.block').removeClass('block').addClass('none');
        constituContentImg.eq(index).removeClass('none').addClass('block');
    })
});



